<template>
    <div id="modal" class="default-modal" v-if="visible">
        <div class="modal-mask" v-show="visibleMask" @click="maskClose"></div>
        <div class="modal-wrap" >
            <div
                class="modal-content"
                :style="{ width: width, height: height }"
            >
                <div class="modal-bg-img">
                    <!-- 插入背景图片 -->
                    <slot name="modalBg"></slot>
                </div>

                <div class="modal-head">
                    <div class="head-title">
                        <img
                            class="head-title-bg"
                            src="../../assets/img/components/img-moadl-title-bg.png"
                        />
                        <div class="title-text">{{ title }}</div>
                    </div>

                    <div
                        class="modal-close"
                        @click="$emit('close', !visible)"
                    ></div>
                </div>
                <div class="modal-body">
                    <slot></slot>
                </div>
            </div>
        </div>
    </div>
</template>

<script>
export default {
    name: "modal",
    model: {
        prop: "visible",
        event: "close"
    },
    props: {
        // 宽度
        width: {
            type: String || Number,
            default: "46.094vw"
        },
        // 高度
        height: {
            type: String || Number,
            default: "26.25vw"
        },
        // 标题
        title: {
            type: String,
            default: ""
        },
        // 对话框是否可见
        visible: {
            type: Boolean,
            default: false
        },
        // 点击蒙层是否允许关闭
        maskClosable: {
            type: Boolean,
            default: false
        },
        // 蒙层是否可见
        visibleMask: {
            type: Boolean,
            default: true
        },
    },

    methods: {
        maskClose() {
            if (this.maskClosable) {
                this.$emit("close", !this.visible);
            }
        }
    }
};
</script>

<style lang="less" scoped>
@import "./index";
</style>
